export default {
	name: "chat-input",
	data() {
		return {
			inputValue: ''
		}
	},
	methods: {
		// startRecognize: function () {
		// 	var options = {};
		// 	var that = this;
		// 	options.engine = 'iFly';
		// 	that.inputValue = "";
		// 	plus.speech.startRecognize(options, function (s) {
		// 		console.log(s);
		// 		that.inputValue += s;
		// 	}, function (e) {
		// 		console.log("语音识别失败：" + e.message);
		// 	});
		// },
		sendMessge: function () {
			var that = this;
			if (that.inputValue.trim() == '') {
				that.inputValue = '';
			} else {

				//点击发送按钮时，通知父组件用户输入的内容
				this.$emit('send-message', {
					type: 'text',
					content: that.inputValue
				});
				that.inputValue = '';
			}
		}
	}
}
